


























































import { Component, Vue, Watch } from "vue-property-decorator";

interface IMapObj {
  type: string;
  contentF: string;
  contentB: string;
  element: HTMLElement;
  innerElements: Array<HTMLImageElement | HTMLIFrameElement>;
  state: {
    x: number;
    y: number;
    z: number;
    thetaXY: number;
    thetaZ: number;
    width: number;
    height: number;
  };
}

interface ISvgPath {
  id: string;
  d: string;
  fill: string;
  stroke: string;
  strokeWidth: string;
  strokeLinejoin: string;
}

interface IVortex {
  x: number;
  y: number;
  z: number;
  link: number[];
}

@Component({
  components: {},
})
export default class List extends Vue {
  private init = false;

  private pathBack: ISvgPath[] = [];
  private pathFront: ISvgPath[] = [];
  private moveT = 0;
  private moveR = 0;
  private dirT = 0;
  private dirR = 0;
  private xSt = 0;
  private ySt = 0;
  private movexSt = 0;
  private moveySt = 0;
  private dirxSt = 0;
  private dirySt = 0;

  private presseDir = false;
  private presseMove = false;
  private onTouchSt(e: TouchEvent) {
    const stickMove = this.$el.querySelector(".move") as HTMLElement;
    const stickDir = this.$el.querySelector(".dir") as HTMLElement;
    for (let idx = 0; idx < e.touches.length; idx++) {
      if (e.touches[idx]?.target === stickDir) {
        this.presseDir = true;
        this.dirxSt = e.touches[idx].screenX;
        this.dirySt = e.touches[idx].screenY;
      }
      if (e.touches[idx]?.target === stickMove) {
        this.presseMove = true;
        this.movexSt = e.touches[idx].screenX;
        this.moveySt = e.touches[idx].screenY;
      }
    }
  }
  private onTouchMv(e: TouchEvent) {
    const stickMove = this.$el.querySelector(".move") as HTMLElement;
    const stickDir = this.$el.querySelector(".dir") as HTMLElement;
    for (let idx = 0; idx < e.touches.length; idx++) {
      if (e.touches[idx]?.target === stickDir) {
        const X = e.touches[idx].screenX - this.dirxSt;
        const Y = e.touches[idx].screenY - this.dirySt;
        let a = Math.sqrt(X * X + Y * Y);
        a = a > 50 ? 50 / a : 1;
        this.dirR = X * a;
        this.dirT = Y * a;
      }
      if (e.touches[idx]?.target === stickMove) {
        const X = e.touches[idx].screenX - this.movexSt;
        const Y = e.touches[idx].screenY - this.moveySt;
        let a = Math.sqrt(X * X + Y * Y);
        a = a > 50 ? 50 / a : 1;
        this.moveR = X * a;
        this.moveT = Y * a;
      }
    }
  }
  private onTouchEd(e: TouchEvent) {
    const stickMove = this.$el.querySelector(".move") as HTMLElement;
    const stickDir = this.$el.querySelector(".dir") as HTMLElement;
    let mvTF = false;
    let dirTF = false;
    for (let idx = 0; idx < e.touches.length; idx++) {
      if (e.touches[idx]?.target === stickDir) {
        dirTF = true;
      }
      if (e.touches[idx]?.target === stickMove) {
        mvTF = true;
      }
    }
    if (!dirTF) {
      this.presseDir = false;
      this.dirR = 0;
      this.dirT = 0;
    }
    if (!mvTF) {
      this.presseMove = false;
      this.moveR = 0;
      this.moveT = 0;
    }
  }

  private onMouceSt(e: MouseEvent) {
    const stickMove = this.$el.querySelector(".move") as HTMLElement;
    const stickDir = this.$el.querySelector(".dir") as HTMLElement;
    if (e.target === stickMove) {
      this.presseMove = true;
      this.xSt = e.screenX;
      this.ySt = e.screenY;
    }
    if (e.target === stickDir) {
      this.presseDir = true;
      this.xSt = e.screenX;
      this.ySt = e.screenY;
    }
  }

  private onMouceMv(e: MouseEvent) {
    const X = e.screenX - this.xSt;
    const Y = e.screenY - this.ySt;
    let a = Math.sqrt(X * X + Y * Y);
    a = a > 50 ? 50 / a : 1;
    if (this.presseMove) {
      this.moveR = (e.screenX - this.xSt) * a;
      this.moveT = (e.screenY - this.ySt) * a;
    }
    if (this.presseDir) {
      this.dirR = (e.screenX - this.xSt) * a;
      this.dirT = (e.screenY - this.ySt) * a;
    }
  }
  private onMouceEd(e: MouseEvent) {
    this.presseMove = false;
    this.presseDir = false;
    this.moveR = 0;
    this.moveT = 0;
    this.dirR = 0;
    this.dirT = 0;
  }

  private enableStick() {
    if ("ontouchstart" in document.documentElement) {
      document.addEventListener("touchstart", this.onTouchSt, false);
      document.addEventListener("touchmove", this.onTouchMv, false);
      document.addEventListener("touchend", this.onTouchEd, false);
      document.addEventListener("touchcancel", this.onTouchEd, false);
    } else {
      document.addEventListener("mousedown", this.onMouceSt, false);
      document.addEventListener("mousemove", this.onMouceMv, false);
      document.addEventListener("mouseup", this.onMouceEd, false);
    }
  }

  private map = {
    xMax: 25,
    yMax: 25,
  };

  private mapVortex: IVortex[] = [
    {
      x: -2,
      y: -2,
      z: 0,
      link: [1, 2, 4],
    },
    {
      x: -2,
      y: -2,
      z: 8,
      link: [0, 3, 5],
    },
    {
      x: this.map.xMax + 2,
      y: -2,
      z: 0,
      link: [3, 0, 6],
    },
    {
      x: this.map.xMax + 2,
      y: -2,
      z: 8,
      link: [2, 1, 7],
    },
    {
      //4
      x: -2,
      y: this.map.yMax + 2,
      z: 0,
      link: [5, 0, 6],
    },
    {
      x: -2,
      y: this.map.yMax + 2,
      z: 8,
      link: [4, 7, 1],
    },
    {
      x: this.map.xMax + 2,
      y: this.map.yMax + 2,
      z: 0,
      link: [7, 2, 4],
    },
    {
      x: this.map.xMax + 2,
      y: this.map.yMax + 2,
      z: 8,
      link: [6, 5, 3],
    },
  ];

  private state = {
    x: 1,
    y: 1,
    z: 5,
    thetaXY: 0,
    thetaZ: 0.7,
  };

  private keyIn = {
    Au: false,
    Ad: false,
    Al: false,
    Ar: false,
    Du: false,
    Dd: false,
    Dl: false,
    Dr: false,
  };
  private moving = false;
  private minimapSize = 0;
  private screenSize = [0, 0];
  private screenRatio = 0;
  private objects: IMapObj[] = [
    {
      type: "img",
      element: document.createElement("div"),
      innerElements: [],
      contentF: "https://dororodoroddo.github.io/img/dog.png",
      contentB: "https://dororodoroddo.github.io/img/back.jpg",
      state: {
        x: 20,
        y: 10,
        z: 5,
        thetaXY: 10,
        thetaZ: 0,
        width: 10,
        height: 10,
      },
    },
    {
      type: "img",
      element: document.createElement("div"),
      innerElements: [],
      contentF: "https://dororodoroddo.github.io/img/cat_transparent.png",
      contentB: "",
      state: {
        x: 13,
        y: 3,
        z: 5,
        thetaXY: 10,
        thetaZ: 90,
        width: 2,
        height: 2,
      },
    },
    {
      type: "img",
      element: document.createElement("div"),
      innerElements: [],
      contentF: "https://dororodoroddo.github.io/img/dot_transparent.png",
      contentB: "",
      state: {
        x: 10,
        y: 20,
        z: 5,
        thetaXY: 10,
        thetaZ: 0,
        width: 8,
        height: 8,
      },
    },
  ];
  private onResize() {
    this.minimapSize = (
      this.$el.querySelector("#minimap") as HTMLLIElement
    ).clientWidth;
    this.screenSize = [window.innerWidth, window.innerHeight];
    this.screenRatio = this.screenSize[1] / this.screenSize[0];
  }

  private mounted() {
    this.onResize();
    document.addEventListener("resize", this.onResize);

    //   
    this.objects.forEach((item) => {
      item.element.style.position = "fixed";
      item.element.style.left = "0px";
      item.element.style.bottom = "0px";
      let itemF: HTMLImageElement | HTMLIFrameElement;
      if (item.type === "img") {
        itemF = document.createElement("img") as HTMLImageElement;
        itemF.src = item.contentF;
        itemF.style.width = "1px";
        itemF.style.height = "1px";
        itemF.style.backfaceVisibility = "hidden";
        item.innerElements.push(itemF);
        if (item.contentB !== "") {
          const itemImgB = document.createElement("img") as HTMLImageElement;
          itemImgB.src = item.contentB;
          itemImgB.style.width = "1px";
          itemImgB.style.height = "1px";
          itemImgB.style.backfaceVisibility = "hidden";
          itemImgB.style.webkitBackfaceVisibility = "hidden";
          item.element.appendChild(itemImgB);
          item.innerElements.push(itemImgB);
        } else {
          itemF!.style.backfaceVisibility = "visible";
          item.innerElements = [itemF!];
        }
      } else if (item.type === "iframe") {
        itemF = document.createElement("iframe") as HTMLIFrameElement;
        itemF.src = item.contentF;
        itemF.style.width = "375px";
        itemF.style.height = "812px";
        itemF.style.backfaceVisibility = "hidden";
        item.innerElements.push(itemF);
        const itemImgB = document.createElement("img") as HTMLImageElement;
        itemImgB.src = item.contentB;
        itemImgB.style.width = "375px";
        itemImgB.style.height = "812px";
        itemImgB.style.backfaceVisibility = "hidden";
        item.element.appendChild(itemImgB);
        item.innerElements.push(itemImgB);
      }
      item.element.appendChild(itemF!);
      this.$el.appendChild(item.element);

      // 
      let dot = document.createElement("div");
      dot.style.position = "absolute";
      dot.style.bottom = `${
        (item.state.y / this.map.yMax) * this.minimapSize
      }px`;
      dot.style.left = `${(item.state.x / this.map.xMax) * this.minimapSize}px`;
      dot.style.width = "2px";
      dot.style.height = "2px";
      dot.style.borderRadius = "50%";
      dot.style.backgroundColor = "blue";
      (this.$el.querySelector("#minimap") as HTMLLIElement).appendChild(dot);
    });
    this.moveObjects();
    const title = "";
    const content =
      "wsad  <br>  .<br><br>  .";
    this.$app.showCommonPopup(title, content, () => {
      document.addEventListener("keydown", this.onKeydown);
      document.addEventListener("keyup", this.onKeyup);
      this.enableStick();
    });
  }
  private beforeDestroy() {
    document.removeEventListener("keydown", this.onKeydown);
    document.removeEventListener("keyup", this.onKeyup);
    document.removeEventListener("resize", this.onResize);
  }
  private onKeydown(e: KeyboardEvent) {
    if (e.metaKey || e.altKey) {
      return;
    }
    if (e.key === "ArrowUp") {
      this.keyIn.Au = true;
    } else if (e.key === "ArrowDown") {
      this.keyIn.Ad = true;
    } else if (e.key === "ArrowRight") {
      this.keyIn.Ar = true;
    } else if (e.key === "ArrowLeft") {
      this.keyIn.Al = true;
    } else if (e.key === "a") {
      this.keyIn.Dl = true;
    } else if (e.key === "") {
      this.keyIn.Dl = true;
    } else if (e.key === "d") {
      this.keyIn.Dr = true;
    } else if (e.key === "") {
      this.keyIn.Dr = true;
    } else if (e.key === "w") {
      this.keyIn.Du = true;
    } else if (e.key === "") {
      this.keyIn.Du = true;
    } else if (e.key === "s") {
      this.keyIn.Dd = true;
    } else if (e.key === "") {
      this.keyIn.Dd = true;
    }
    if (!this.moving) {
      this.moving = true;
      requestAnimationFrame(this.move);
    }
  }
  private onKeyup(e: KeyboardEvent) {
    if (e.key === "ArrowUp") {
      this.keyIn.Au = false;
    } else if (e.key === "ArrowDown") {
      this.keyIn.Ad = false;
    } else if (e.key === "ArrowRight") {
      this.keyIn.Ar = false;
    } else if (e.key === "ArrowLeft") {
      this.keyIn.Al = false;
    } else if (e.key === "a") {
      this.keyIn.Dl = false;
    } else if (e.key === "") {
      this.keyIn.Dl = false;
    } else if (e.key === "d") {
      this.keyIn.Dr = false;
    } else if (e.key === "") {
      this.keyIn.Dr = false;
    } else if (e.key === "w") {
      this.keyIn.Du = false;
    } else if (e.key === "") {
      this.keyIn.Du = false;
    } else if (e.key === "s") {
      this.keyIn.Dd = false;
    } else if (e.key === "") {
      this.keyIn.Dd = false;
    } else if (e.key === "F1") {
      const title = "(F1)";
      const content =
        "wsad  <br>  .<br><br>  .";
      this.$app.showCommonPopup(title, content);
    }
    if (!Object.values(this.keyIn).includes(true)) {
      this.moving = false;
    }
  }
  private move() {
    // 
    if (this.keyIn.Au && !this.keyIn.Ad) {
      this.state.y += Math.sin(this.state.thetaZ) / 3;
      this.state.x += Math.cos(this.state.thetaZ) / 3;
    } else if (!this.keyIn.Au && this.keyIn.Ad) {
      this.state.y -= Math.sin(this.state.thetaZ) / 3;
      this.state.x -= Math.cos(this.state.thetaZ) / 3;
    }
    if (this.keyIn.Ar && !this.keyIn.Al) {
      this.state.y += Math.sin(this.state.thetaZ - Math.PI / 2) / 3;
      this.state.x += Math.cos(this.state.thetaZ - Math.PI / 2) / 3;
    } else if (!this.keyIn.Ar && this.keyIn.Al) {
      this.state.y -= Math.sin(this.state.thetaZ - Math.PI / 2) / 3;
      this.state.x -= Math.cos(this.state.thetaZ - Math.PI / 2) / 3;
    }
    //  
    if (this.state.y > this.map.yMax) {
      this.state.y = this.map.yMax;
    } else if (this.state.y < 0) {
      this.state.y = 0;
    }
    if (this.state.x > this.map.xMax) {
      this.state.x = this.map.xMax;
    } else if (this.state.x < 0) {
      this.state.x = 0;
    }
    //  
    if (this.keyIn.Du && !this.keyIn.Dd) {
      this.state.thetaXY =
        this.state.thetaXY + 0.0115 > 0.33
          ? this.state.thetaXY
          : this.state.thetaXY + 0.0115;
    } else if (!this.keyIn.Du && this.keyIn.Dd) {
      this.state.thetaXY =
        this.state.thetaXY - 0.0115 < -0.33
          ? this.state.thetaXY
          : this.state.thetaXY - 0.0115;
    }
    if (this.keyIn.Dr && !this.keyIn.Dl) {
      this.state.thetaZ -= 0.023;
    } else if (!this.keyIn.Dr && this.keyIn.Dl) {
      this.state.thetaZ += 0.023;
    }
    //  
    if (this.state.thetaZ > 2 * Math.PI) {
      this.state.thetaZ -= 2 * Math.PI;
    } else if (this.state.thetaZ < 0) {
      this.state.thetaZ += 2 * Math.PI;
    }
    if (this.state.thetaXY > 1) {
      this.state.thetaXY = 1;
    } else if (this.state.thetaXY < -1) {
      this.state.thetaXY = -1;
    }
    this.moveObjects();
    if (this.moving) {
      requestAnimationFrame(this.move);
    }
  }

  @Watch("presseDir")
  private onChangeDir(a: boolean) {
    if (a && !this.moving) {
      requestAnimationFrame(this.stickMove);
      this.moving = true;
      return;
    }
    if (!a && !this.presseMove) {
      this.moving = false;
    }
  }

  @Watch("presseMove")
  private onChangeMove(a: boolean) {
    if (a && !this.moving) {
      requestAnimationFrame(this.stickMove);
      this.moving = true;
    }
    if (!a && !this.presseDir) {
      this.moving = false;
    }
  }

  private stickMove() {
    // 
    this.state.y -=
      ((Math.sin(this.state.thetaZ) / 3) * this.moveT) / 200 -
      ((Math.sin(this.state.thetaZ - Math.PI / 2) / 3) * this.moveR) / 200;
    this.state.x -=
      ((Math.cos(this.state.thetaZ) / 3) * this.moveT) / 200 -
      ((Math.cos(this.state.thetaZ - Math.PI / 2) / 3) * this.moveR) / 200;
    //  
    if (this.state.y > this.map.yMax) {
      this.state.y = this.map.yMax;
    } else if (this.state.y < 0) {
      this.state.y = 0;
    }
    if (this.state.x > this.map.xMax) {
      this.state.x = this.map.xMax;
    } else if (this.state.x < 0) {
      this.state.x = 0;
    }

    //  
    this.state.thetaXY -= this.dirT / 1500;
    this.state.thetaZ -= this.dirR / 1200;

    //  
    if (this.state.thetaZ > 2 * Math.PI) {
      this.state.thetaZ -= 2 * Math.PI;
    } else if (this.state.thetaZ < 0) {
      this.state.thetaZ += 2 * Math.PI;
    }
    if (this.state.thetaXY > 0.33) {
      this.state.thetaXY = 0.33;
    } else if (this.state.thetaXY < -0.33) {
      this.state.thetaXY = -0.33;
    }

    this.moveObjects();
    if (this.moving) {
      requestAnimationFrame(this.stickMove);
    }
  }

  private moveObjects() {
    this.objects.forEach((item) => {
      const { relX, relY, relAngleZ, relAngleXY } = this.getRelativeProperty(
        item.state.x,
        item.state.y,
        item.state.z
      );
      //     
      if (Math.abs(relAngleZ) < 1) {
        if (item.element.style.opacity === "0") {
          item.element.style.opacity = "1";
        }
        let r = Math.sqrt(relX * relX + relY * relY);
        let w =
          ((4 * Math.atan(item.state.width / 2 / r)) / Math.PI) *
          this.screenSize[0];
        let h =
          ((4 * Math.atan(item.state.height / 2 / r)) / Math.PI) *
          this.screenSize[0];

        let dirX = (item.state.thetaXY * Math.PI) / 180;
        let dirY =
          (item.state.thetaZ * Math.PI) / 180 -
          Math.atan2(-relY, -relX) -
          Math.PI;
        item.element.style.transform = `translate(${
          this.screenSize[0] / 2 - relAngleZ * 2 * this.screenSize[0]
        }px, ${-(
          this.screenSize[1] / 2 -
          this.state.thetaXY * 2 * this.screenSize[1]
        )}px)`;
        if (item.type === "img") {
          item.innerElements[0].style.transform = `scaleX(${w}) scaleY(${h}) perspective(${r}px) rotateY(${dirY}rad) rotateX(${dirX}rad)`;
        } else {
          item.innerElements[0].style.transform = `scaleX(${w / 375}) scaleY(${
            h / 812
          }) perspective(${r}px) rotateY(${dirY}rad) rotateX(${dirX}rad)`;
        }
        if (item.innerElements.length === 2) {
          item.innerElements[1].style.transform = `scaleX(${w}) scaleY(${h}) perspective(${r}px) rotateY(${
            dirY + Math.PI
          }rad) rotateX(${-dirX}rad)`;
        }
        item.element.style.zIndex = `${
          this.map.xMax + this.map.yMax - Math.floor(r)
        }`;
      } else {
        if (item.element.style.opacity !== "0") {
          item.element.style.opacity = "0";
        }
      }
    });
    this.pathBack = [];
    const newVortexs: { x: number; y: number; link: number[]; relZ: number }[] =
      this.mapVortex.map((item, index) => {
        const { relAngleZ, relAngleXY } = this.getRelativeProperty(
          item.x,
          item.y,
          item.z
        );
        let x = this.screenSize[0] / 2 - relAngleZ * 2 * this.screenSize[0];
        let y = this.screenSize[0] / 2 - relAngleXY * 2 * this.screenSize[0];
        const link = [...item.link];
        return { x, y, link, relZ: relAngleZ };
      });
    for (let i of [0, 3, 5, 6]) {
      for (let j of newVortexs[i].link) {
        if (
          Math.abs(newVortexs[i].relZ) < 1.4 ||
          Math.abs(newVortexs[j].relZ) < 1.4
        ) {
          this.pathBack.push(
            this.defaultPath(newVortexs[i], newVortexs[j], `${i}${j}`)
          );
        }
      }
    }
  }

  private defaultPath(
    start: { x: number; y: number; link: number[] },
    end: { x: number; y: number; link: number[] },
    id: string
  ): ISvgPath {
    return {
      id,
      d: `M ${start.x} ${start.y} L${end.x} ${end.y}`,
      fill: "",
      stroke: "#000",
      strokeWidth: "1",
      strokeLinejoin: "round",
    };
  }
  private getRelativeProperty(x: number, y: number, z: number) {
    //  ,  
    let relX = x - this.state.x;
    let relY = y - this.state.y;
    let relZ = z - this.state.z;
    let relAngleZ = Math.atan2(relY, relX) - this.state.thetaZ;
    // relAngleXY ;
    //   
    while (relAngleZ < -Math.PI || relAngleZ > Math.PI) {
      if (relAngleZ < -Math.PI) {
        relAngleZ += 2 * Math.PI;
      } else {
        relAngleZ -= 2 * Math.PI;
      }
    }
    let relAngleXY =
      Math.atan2(
        relZ,
        (Math.cos(relAngleZ) / 2) * Math.sqrt(relX * relX + relY * relY)
      ) - this.state.thetaXY;
    while (relAngleXY < -Math.PI || relAngleXY > Math.PI) {
      if (relAngleXY < -Math.PI) {
        relAngleXY += 2 * Math.PI;
      } else {
        relAngleXY -= 2 * Math.PI;
      }
    }
    return { relX, relY, relZ, relAngleZ, relAngleXY };
  }

  private meTransform() {
    return `translate(${(this.state.x / this.map.xMax) * this.minimapSize}px,-${
      (this.state.y / this.map.yMax) * this.minimapSize
    }px)  rotate(${45 - (this.state.thetaZ * 180) / Math.PI}deg)`;
  }
}
